html {
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
}

h1 {
  margin: .67em 0;
  font-size: 2em;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace;
  font-size: 1em;
}

a {
  background-color: #0000;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp {
  font-family: monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

img {
  border-style: none;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, input {
  overflow: visible;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring, [type="button"]:-moz-focusring, [type="reset"]:-moz-focusring, [type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: .35em .75em .625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  max-width: 100%;
  white-space: normal;
  padding: 0;
  display: table;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"], [type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button, [type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template, [hidden] {
  display: none;
}

*, :before, :after {
  box-sizing: border-box;
}

html, body, div, span, h1, h2, h3, h4, h5, address, img, strong, i, p, center, ul, ol, li, form, label, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, footer, header, hgroup, menu, nav, section, input {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

article, aside, footer, header, hgroup, menu, nav, section {
  display: block;
}

a {
  outline: none;
  text-decoration: none;
}

ul, ol {
  list-style: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 100%;
}

@font-face {
  font-family: Gilroy-Bold;
  src: url("Gilroy-Bold.6b26fd6d.woff2") format("woff2"), url("Gilroy-Bold.33c7fed6.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: block;
}

@font-face {
  font-family: Gilroy-Semibold;
  src: url("Gilroy-Semibold.eff0d7ae.woff2") format("woff2"), url("Gilroy-Semibold.83b64308.woff") format("woff");
  font-style: normal;
  font-weight: 600;
  font-display: block;
}

@font-face {
  font-family: Gilroy-SemiboldItalic;
  src: url("Gilroy-SemiboldItalic.30c520e3.woff2") format("woff2"), url("Gilroy-SemiboldItalic.9f104c3a.woff") format("woff");
  font-style: normal;
  font-weight: 600;
  font-display: block;
}

@font-face {
  font-family: Gilroy-Medium;
  src: url("Gilroy-Medium.c7e88611.woff2") format("woff2"), url("Gilroy-Medium.3e718dd4.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: block;
}

h1 {
  font-family: Gilroy-Bold, sans-serif;
  font-style: normal;
  font-weight: 400;
}

h2 {
  letter-spacing: -.15rem;
  font-family: Gilroy-Bold, sans-serif;
  font-size: 4rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.125;
}

h3 {
  letter-spacing: -.06875rem;
  margin-bottom: 1.25rem;
  font-family: Gilroy-Bold, sans-serif;
  font-size: 2.5rem;
  font-style: normal;
  font-weight: 400;
  line-height: 3rem;
}

h4 {
  font-family: Gilroy-Bold, sans-serif;
  font-size: 2rem;
  font-style: normal;
  font-weight: 400;
  line-height: 2.5rem;
}

.text-violet-color {
  color: #ac09e6;
}

.link {
  color: #2731ff;
  font-size: 1.125rem;
  line-height: 1.75rem;
  text-decoration: none;
  transition: opacity .25s;
}

.link:hover {
  opacity: .7;
}

.link:active {
  opacity: .55;
}

.link--white {
  color: #fff;
}

.link--black {
  color: #0b1621;
}

p {
  font-size: 1.125rem;
  line-height: 1.75rem;
}

.wrapper {
  width: 100%;
  max-width: 1296px;
  margin: 0 auto;
  padding: 0 40px;
}

@media (max-width: 640px) {
  .wrapper {
    padding: 0 16px;
  }
}

.row {
  gap: 32px;
  display: flex;
}

@media (max-width: 960px) {
  .row {
    flex-wrap: wrap;
    justify-content: center;
  }
}

.col--2 {
  width: 50%;
}

.col-4 {
  width: 25%;
}

.container {
  min-height: 100vh;
  min-width: 100vw;
  position: relative;
}

.main {
  z-index: 1;
  position: relative;
}

.main--catalog {
  background: #e3ebef;
  padding-top: 64px;
}

.main--cabinet {
  min-height: 100vh;
  overflow: hidden;
}

.main--collection {
  background: #f2f8fa;
}

.header {
  width: 100%;
  z-index: 15;
  justify-content: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.header--catalog .header__wr {
  padding: 8px 24px;
}

@media (max-width: 640px) {
  .header--catalog .header__wr {
    padding: 8px 16px;
  }
}

@media (max-width: 960px) {
  .header--catalog .btn, .header--catalog .wallet-btn {
    display: none;
  }
}

.header--cabinet {
  background-color: #fff;
  box-shadow: 0 2px 4px #0000001a;
}

.header--cabinet .header__wr {
  padding: 8px 24px;
}

@media (max-width: 640px) {
  .header--cabinet .header__wr {
    padding: 8px 16px;
  }
}

.header--cabinet .header__right .btn + .wallet-btn {
  margin-left: 16px;
}

.header--senti .header__wr {
  padding: 24px 32px;
}

@media (max-width: 640px) {
  .header--senti .header__wr {
    padding: 16px;
  }
}

@media (max-width: 960px) {
  .header--senti .header-links {
    gap: 24px;
    margin-right: 32px;
  }
}

@media (max-width: 640px) {
  .header--senti .header-links, .header--senti .btn {
    display: none;
  }
}

.header__wr {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  display: flex;
}

@media (max-width: 640px) {
  .header__wr {
    padding: 16px;
  }
}

.header__right {
  display: flex;
}

.header__right .btn + .wallet-btn {
  margin-left: 12px;
}

.menu-btn {
  width: 40px;
  height: 40px;
  padding: 4px;
  display: none;
  position: relative;
}

@media (max-width: 960px) {
  .menu-btn {
    display: block;
  }
}

.menu-btn__line {
  width: 100%;
  height: 3px;
  background-color: #0d0b21;
  border-radius: 2px;
  margin: 5px 0;
  transition: transform .2s;
}

.menu-btn--active .menu-btn__line-1 {
  transform: translateY(8px)translateX(0)rotate(45deg);
}

.menu-btn--active .menu-btn__line-2 {
  opacity: 0;
}

.menu-btn--active .menu-btn__line-3 {
  transform: translateY(-8px)translateX(0)rotate(-45deg);
}

.mobile-menu {
  height: 100vh;
  width: 100%;
  z-index: 10;
  opacity: 0;
  visibility: hidden;
  background-color: #fff;
  transition: all .25s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(100vh);
}

.mobile-menu--active {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.mobile-menu__wr {
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10vh 16px;
  display: flex;
}

.mobile-menu__item {
  margin-bottom: 16px;
  padding: 16px 24px;
}

.mobile-menu__item .link {
  font-size: 22px;
  line-height: 1.5;
}

.mobile-menu__btn {
  width: 155px;
  margin-bottom: 24px;
}

.mobile-menu__btn:last-child {
  margin-bottom: 0;
}

.mobile-menu__btn .btn {
  width: 100%;
}

.mobile-menu__btn .btn__text {
  text-align: center;
  font-size: 18px;
}

.section {
  width: 100%;
  margin-bottom: 100px;
}

.section--senti-features {
  margin-bottom: 8.4375rem;
}

@media (max-width: 640px) {
  .section--senti-features {
    margin-bottom: 5rem;
  }
}

.section--senti-features .section__head {
  margin-bottom: 3.875rem;
}

.section--senti-features .section__body {
  margin-bottom: 3.25rem;
}

.section--senti-features .feature-item__title {
  margin-bottom: .75rem;
}

.section--faq {
  margin-bottom: 4.625rem;
}

.section--faq .section__head {
  margin-bottom: 4rem;
}

.section--features {
  margin-bottom: 16.75rem;
}

@media (max-width: 640px) {
  .section--features {
    margin-bottom: 9.375rem;
  }
}

.section--tools {
  margin-bottom: 7.9375rem;
}

@media (max-width: 640px) {
  .section--tools {
    margin-bottom: 0;
  }
}

.section--tools .section__head {
  margin-bottom: 13.875rem;
}

@media (max-width: 640px) {
  .section--tools .section__head {
    margin-bottom: 5rem;
  }
}

.section__head {
  max-width: 800px;
  text-align: center;
  margin: 0 auto 84px;
}

.section__body {
  margin-bottom: 68px;
}

.section__btn {
  justify-content: center;
  display: flex;
}

.footer {
  z-index: 2;
  width: 100%;
  padding: 16px;
  position: relative;
}

.footer--senti .footer__wr {
  background-color: #0b1621;
  padding: 5.375rem 0 4.1875rem;
}

.footer--senti .footer__top {
  margin-bottom: 4.875rem;
}

.footer--senti .footer__top .logo-wr {
  margin-top: -6px;
  margin-left: -10px;
  margin-right: 10px;
}

.footer--senti .link-list__title {
  color: #909ca5;
}

.footer--senti .copyright {
  color: #b2bbc1;
}

.footer--senti .terms-item a {
  color: #fff;
}

.footer--senti .terms-item a:hover {
  opacity: .8;
}

.footer__wr {
  width: 100%;
  background-color: #ffffff8f;
  border-radius: 24px;
  padding: 5.125rem 0 4.1875rem;
}

@media (max-width: 640px) {
  .footer__wr {
    padding: 40px 0;
  }
}

.footer__top {
  gap: 32px;
  margin-bottom: 7.625rem;
  display: flex;
}

@media (max-width: 640px) {
  .footer__top {
    flex-wrap: wrap;
    gap: 16px;
    margin-bottom: 60px;
    padding: 0 10px;
  }
}

.footer__top > * {
  width: 25%;
}

@media (max-width: 640px) {
  .footer__top > * {
    width: calc(50% - 16px);
  }
}

.footer__top .logo-wr {
  margin-top: -12px;
}

@media (max-width: 640px) {
  .footer__top .logo-wr {
    width: 100%;
    margin: 0 0 22px -5px;
  }
}

.footer__bottom {
  justify-content: space-between;
  gap: 32px;
  display: flex;
}

@media (max-width: 640px) {
  .footer__bottom {
    flex-wrap: wrap;
    gap: 16px;
    padding: 0 10px;
  }
}

.link-list__title {
  margin-bottom: 24px;
  font-family: Gilroy-Semibold, sans-serif;
  font-size: 1.3125rem;
  font-style: normal;
  font-weight: 600;
  line-height: 1.143;
}

.link-list__item {
  margin-bottom: 16px;
}

.link-list__item a {
  padding: 3px 0;
}

.link-list__item:last-child {
  margin-bottom: 0;
}

.copyright {
  color: #566576;
  font-size: 1rem;
  line-height: 1.25;
}

.terms {
  display: flex;
}

.terms-item + .terms-item {
  margin-left: 32px;
}

.terms-item a {
  color: #000;
  font-size: 1rem;
  line-height: 1.25;
}

.terms-item a:hover {
  opacity: 1;
  text-decoration: underline;
}

.join-section {
  z-index: 2;
  padding: 5rem 0;
  position: relative;
}

.join-section--catalog {
  padding: 7.5rem 0 4.8125rem;
}

.join-section--senti {
  padding: 5.9375rem 0 4.8125rem;
}

@media (max-width: 640px) {
  .join-section--senti {
    padding: 5rem 0 4.8125rem;
  }

  .join-section--senti h2 {
    font-size: 3.375rem;
  }
}

.join-section__head {
  text-align: center;
  margin-bottom: 1.25rem;
}

.join-section__text {
  max-width: 50rem;
  text-align: center;
  margin: 0 auto 3.875rem;
}

.join-section__list {
  justify-content: center;
}

@media (max-width: 960px) {
  .join-section__list {
    gap: 16px;
  }
}

.join-item {
  width: 25%;
  background-color: #fff;
  border-radius: 16px;
  transition: box-shadow .25s;
  position: relative;
}

.join-item:hover {
  box-shadow: 0 20px 40px #0d0b2129;
}

@media (max-width: 1152px) {
  .join-item {
    width: calc(50% - 8px);
    max-width: 250px;
  }
}

.join-item:after {
  content: "";
  float: left;
  padding-top: 100%;
}

.join-item__wr {
  width: 100%;
  height: 100%;
  z-index: 1;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 3rem 1.5rem;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

@media (max-width: 640px) {
  .join-item__wr {
    justify-content: center;
    padding: 1.5rem;
  }
}

.join-item__img {
  width: 52%;
  max-width: 120px;
  height: 7.5rem;
}

@media (max-width: 640px) {
  .join-item__img {
    height: 80px;
    margin-bottom: 16px;
  }
}

.join-item__img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  overflow: hidden;
}

.join-item__name {
  font-family: Gilroy-Semibold, sans-serif;
  font-size: 1.3125rem;
  font-style: normal;
  font-weight: 600;
}

.join-item__link {
  width: 100%;
  height: 100%;
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
}

