.disable-scroll {
  overflow: hidden;
}

.logo {
  width: 130px;
  height: 48px;
  background: url("logo.25e0381d.svg") center no-repeat;
  transition: opacity .25s;
  display: block;
}

.logo:hover {
  opacity: .8;
}

.btn {
  opacity: 1;
  user-select: none;
  cursor: pointer;
  border-radius: 16px;
  padding: 1rem 1.25rem .875rem;
  transition: all .25s;
  display: inline-block;
}

.btn:active {
  opacity: .9;
}

.btn--grey {
  background-color: #0d0b21;
}

.btn--grey:hover {
  background-color: #0d0b21d9;
}

.btn--blue {
  background-color: #2731ff;
}

.btn--blue:hover {
  background-color: #2731ffcc;
}

.btn--light-blue {
  background-color: #f2f8fa;
}

.btn--light-blue:hover {
  background-color: #e7f2f6;
}

.btn--light-blue .btn__text {
  color: #000;
}

.btn--deep-blue {
  background-color: #3f3bed;
}

.btn--deep-blue:hover {
  background-color: #514eef;
}

.btn--white {
  background-color: #fff;
}

.btn--white .btn__text {
  color: #0b1621;
}

.btn--white:hover {
  background-color: #e7f2f6;
}

.btn--big {
  border-radius: 24px;
  padding: 1.4375rem 2.5rem 1.25rem;
}

.btn--big .btn__text {
  font-size: 1.3125rem;
}

.btn--flex {
  text-align: center;
  flex: 1;
}

.btn__text {
  color: #fff;
  font-family: Gilroy-Semibold, sans-serif;
  font-size: 1.125rem;
  font-style: normal;
  font-weight: 600;
  line-height: 1;
}

.wallet-btn {
  opacity: 1;
  user-select: none;
  cursor: pointer;
  border: 1px solid #0d0b21;
  border-radius: 16px;
  align-items: center;
  padding: .6875rem .75rem .6875rem .9375rem;
  transition: all .25s;
  display: inline-flex;
}

.wallet-btn:hover {
  opacity: .7;
}

.wallet-btn:active {
  opacity: .6;
}

.wallet-btn--blue {
  background: #f2f8fa;
  border: none;
}

.wallet-btn--blue:hover {
  opacity: 1;
  background: #e7f2f6;
}

.wallet-btn--blue:active {
  opacity: .8;
}

.wallet-btn__icon {
  width: 20px;
  height: 18px;
  background: url("wallet.a04e7333.svg") center no-repeat;
